@import '../../common/variables';
@import '../../common/mixins';

.container {
  position: relative;
  padding: 80px 0;
}

.background, .title {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-transform: uppercase;
  text-align: center;
}

.background {
  font-size: 11rem;
  letter-spacing: 10px;
  line-height: .7;
  font-weight: 800;
  color: rgba(30,37,48,.07);
}

.title {
  font-size: 5.6rem;
  font-weight: 900;
  margin: 0;
  color: $textColor;
  white-space: nowrap;
}

.subTitle {
  color: rgba(255, 180, 0, 0.85);
  margin-left: 10px;
}

@media (max-width: 600px) {
.container {
  padding: 40px 0;
  margin-bottom: 20px;
}
  .background{
    font-size: 4rem;
  }
  .title {
    top: 65%;
    font-size: 3rem;
  }
}


